.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 120px;
  display: flex;
  align-items: center;
  background-color: black;
  justify-content: center;
  font-weight: 600;
  border-bottom: 2px solid #441e1f;
}
.container-titulo {
  margin-left: 20px;
  justify-content: left;
}
.nome-estabelecimento {
  color: #fbaf1b;
  font-size: 20px;
  text-align: center;
  font-weight: bold;
}

.telefone-estabelecimento {
  color: #fbaf1b;
  font-size: 14px;
  text-align: center;
  font-weight: bold;
}

.status-estabelecimento {
  text-align: center;
  color: #441e1f;
  font-size: 18px;
  border-radius: 10px;
  border: 1px solid;
  padding: 5px;
}

.titulo {
  font-style: italic;
  margin: 20px;
}
.imgLogo {
  width: 100px;
  height: 75px;
}
